import { Component, OnInit } from '@angular/core';
import { StdService } from 'src/app/shared/services/std.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-std-sidebar',
  templateUrl: './std-sidebar.component.html',
  styleUrls: ['./std-sidebar.component.css']
})
export class StdSidebarComponent implements OnInit {
stdData : any = [];
  constructor(private service: StdService, private router: Router) { }

  ngOnInit() {
    this.stdData = this.service.getData();
  }
  logout() {
    localStorage.clear();
    this.router.navigate(['/stdLogin']);
  }
}
