import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { Router } from '@angular/router';

@Component({
  selector: 'app-pheader',
  templateUrl: './pheader.component.html',
  styleUrls: ['./pheader.component.css']
})
export class PheaderComponent implements OnInit {

  @Output() toggleSideBarOnClick: EventEmitter<any> = new EventEmitter();
  constructor(private router: Router) { }

  ngOnInit() {
  }
  toggleSideBar() {
    this.toggleSideBarOnClick.emit();
    setTimeout(() => {
      window.dispatchEvent(
        new Event('resize')
    );
    }, 300);
  }
  logout() {
    localStorage.clear();
    this.router.navigate(['/parentLogin']);
  }

}
