import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ParentService } from 'src/app/shared/services/parent.service';

@Component({
  selector: 'app-psidebar',
  templateUrl: './psidebar.component.html',
  styleUrls: ['./psidebar.component.css']
})
export class PsidebarComponent implements OnInit {
parentData: any = [];
  constructor(private router: Router, private service: ParentService) { }

  ngOnInit() {
    this.parentData = this.service.getData();
  }
  logout() {
    localStorage.clear();
    this.router.navigate(['/parentLogin']);
  }

}
