import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { Std } from '../Models/stdModel';
import { Router } from '@angular/router';

@Injectable({
  providedIn: 'root'
})
export class StdService {

  registerUrl = 'http://localhost:7000/std/register';
  loginUrl = 'http://localhost:7000/std/login';
  user = '';

  constructor(private http: HttpClient, private router: Router) {}

  register(data) {
    console.log(data);
    return this.http.post(this.registerUrl, data);
  }
  login(data) {
    console.log(data);
    const headers = new HttpHeaders();
    headers.append('Content-Type', 'application/json');
    return this.http.post(this.loginUrl, data, {headers});
  }
  StoreUserData(data) {
    console.log('we are at storage');
    console.log(data.token);
    console.log(data.data.std);
    localStorage.setItem('access_token', data.data.token);
    localStorage.setItem('student', JSON.stringify(data.data.std));
    this.user = data.data.std;
    this.router.navigate(['/stdProfile']);
  }
  isLoggedIn() {
    return localStorage.getItem('access_token') != null;
  }
  getData() {
    return this.user;
  }
}
