import { Injectable } from '@angular/core';
import { HttpHeaders, HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';

@Injectable({
  providedIn: 'root'
})
export class AdminService {

  user = '';
  registerUrl = 'http://localhost:7000/admin/register';
  loginUrl = 'http://localhost:7000/admin/login';
  allParentUrl = 'http://localhost:7000/secure/allParents';
  allStdUrl = 'http://localhost:7000/secure/allStds';
  allTutorsUrl = 'http://localhost:7000/secure/allTutors';

  constructor(private http: HttpClient, private router: Router) { }

  register(data) {
    console.log(data);
    return this.http.post(this.registerUrl, data);
  }
  login(data) {
    console.log(data);
    const headers = new HttpHeaders();
    headers.append('Content-Type', 'application/json');
    return this.http.post(this.loginUrl, data, {headers});
  }
  StoreUserData(data) {
    console.log('we are at storage');
    console.log(data.token);
    console.log(data.data.admin);
    localStorage.setItem('access_token', data.data.token);
    localStorage.setItem('admin', JSON.stringify(data.data.admin));
    this.user = data.data.admin;
    console.log('user data ' + this.user);
    this.router.navigate(['/admin']);
  }
  isLoggedIn() {
    return localStorage.getItem('access_token') != null;
  }
  getData() {
    return this.user;
  }
  allParents() {
    return this.http.get(this.allParentUrl);
  }
  allStds() {
    return this.http.get(this.allStdUrl);
  }
  allTutors() {
    return this.http.get(this.allTutorsUrl);
  }
  tutorsChart() {
    return [1, 209, 405, 898, 1600, 3208, 646, 1208, 2506, 512, 3575];
  }
  parentsChart() {
    return [1, 20, 45, 87, 16, 320, 64, 128, 256, 512, 1235];
  }
  stdsChart() {
    return [1, 2, 40, 808, 16, 332, 664, 128, 2576, 512, 1575];
  }
}
