import { Component, OnInit } from '@angular/core';
import { ParentService } from '../../../shared/services/parent.service';

@Component({
  selector: 'app-parent-login',
  templateUrl: './parent-login.component.html',
  styleUrls: ['./parent-login.component.css']
})
export class ParentLoginComponent implements OnInit {

  constructor(private service: ParentService) { }

  ngOnInit() {
  }
  login(fullform) {
    this.service.login(fullform.value).subscribe(data => {
      if (data) {
        this.service.StoreUserData(data);
      }
    }, err => {
      console.error(err);
    });
    }
}
