import { Component, OnInit } from '@angular/core';
import { TutorService } from '../../../shared/services/tutor.service';

@Component({
  selector: 'app-tutor-login',
  templateUrl: './tutor-login.component.html',
  styleUrls: ['./tutor-login.component.css']
})
export class TutorLoginComponent implements OnInit {

  constructor(private service: TutorService) { }

  ngOnInit() {
  }
  login(fullform) {
    this.service.login(fullform.value).subscribe(data => {
      if (data) {
        this.service.StoreUserData(data);
      }
    }, err => {
      console.error(err);
    });
    }
}
