import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ParentGuard } from './shared/guards/parent.guard';
import { AdminGuard } from './shared/guards/admin.guard';
import { TutorGuard } from './shared/guards/tutor.guard';
import { StdGuard } from './shared/guards/std.guard';

import { HomeHomepageComponent } from './home/home-homepage/home-homepage.component';
import { UserRegisterComponent } from './home/user-register/user-register.component';
import { AboutUsComponent } from './home/about-us/about-us.component';
import { PnfoundComponent } from './home/pnfound/pnfound.component';
import { ContactUsComponent } from './home/contact-us/contact-us.component';
import { DefaultComponent } from './admin/default/default.component';
import { DashboardComponent } from './admin/components/dashboard/dashboard.component';
import { StudentsComponent } from './admin/components/students/students.component';
import { TutorsComponent } from './admin/components/tutors/tutors.component';
import { ParentsComponent } from './admin/components/parents/parents.component';
import { ProfileComponent } from './admin/components/profile/profile.component';
import { SettingsComponent } from './admin/components/settings/settings.component';
import { MessagesComponent } from './admin/components/messages/messages.component';
import { StudentRegisterComponent } from './home/registerForms/student-register/student-register.component';
import { TutorRegisterComponent } from './home/registerForms/tutor-register/tutor-register.component';
import { ParentRegisterComponent } from './home/registerForms/parent-register/parent-register.component';
import { ParentLoginComponent } from './home/login/parent-login/parent-login.component';
import { StdloginComponent } from './home/login/stdlogin/stdlogin.component';
import { TutorLoginComponent } from './home/login/tutor-login/tutor-login.component';
import { AdminLoginComponent } from './home/login/admin-login/admin-login.component';
import { AdminRegisterComponent } from './home/registerForms/admin-register/admin-register.component';
import { TutorDefaultComponent } from './tutorPanel/default/default.component';
import { TutorDashboardComponent } from './tutorPanel/components/dashboard/dashboard.component';
import { TutorMessagesComponent } from './tutorPanel/components/messages/messages.component';
import { TutorProfileComponent } from './tutorPanel/components/profile/profile.component';
import { TutorSettingsComponent } from './tutorPanel/components/settings/settings.component';
import { TutorStudentsComponent } from './tutorPanel/components/students/students.component';
import { TutorParentsComponent } from './tutorPanel/components/tutor-parents/tutor-parents.component';
import { StdDefaultComponent } from './studentPanel/std-default/std-default.component';
import { StdDashboardComponent } from './studentPanel/components/std-dashboard/std-dashboard.component';
import { StdProfileComponent } from './studentPanel/components/std-profile/std-profile.component';
import { StdMessagesComponent } from './studentPanel/components/std-messages/std-messages.component';
import { StdSettingsComponent } from './studentPanel/components/std-settings/std-settings.component';
import { StdTutorsComponent } from './studentPanel/components/std-tutors/std-tutors.component';
import { PDefaultComponent } from './parentPanel/p-default/p-default.component';
import { PdashboardComponent } from './parentPanel/components/pdashboard/pdashboard.component';
import { PprofileComponent } from './parentPanel/components/pprofile/pprofile.component';
import { PmessagesComponent } from './parentPanel/components/pmessages/pmessages.component';
import { PsettingsComponent } from './parentPanel/components/psettings/psettings.component';
import { PtutorsComponent } from './parentPanel/components/ptutors/ptutors.component';


const routes: Routes = [
  {path: '', component: HomeHomepageComponent},
  {path: 'home', component: HomeHomepageComponent},
  {path: 'registrations', component: UserRegisterComponent},
  {path: 'about', component: AboutUsComponent},
  {path: 'stdRegister', component: StudentRegisterComponent},
  {path: 'tutorRegister', component: TutorRegisterComponent},
  {path: 'parentRegister', component: ParentRegisterComponent},
  {path: 'contact', component: ContactUsComponent},
  {path: 'stdLogin', component: StdloginComponent},
  {path: 'parentLogin', component: ParentLoginComponent},
  {path: 'tutorLogin', component: TutorLoginComponent},
  {path: 'adminLogin', component: AdminLoginComponent},
  {path: 'adminRegister', component: AdminRegisterComponent},
  {
    path: 'admin', canActivate: [AdminGuard], component: DefaultComponent,
    children: [
      {path: '', component: DashboardComponent},
      {path: 'students', component: StudentsComponent},
      {path: 'tutors', component: TutorsComponent},
      {path: 'parents', component: ParentsComponent},
      {path: 'profile', component: ProfileComponent},
      {path: 'settings', component: SettingsComponent},
      {path: 'messages', component: MessagesComponent}
    ]
  },
  {
    path: 'tutorProfile', canActivate: [TutorGuard], component: TutorDefaultComponent,
    children: [
      {path: '', component: TutorDashboardComponent},
      {path: 'messages', component: TutorMessagesComponent},
      {path: 'profile', component: TutorProfileComponent},
      {path: 'settings', component: TutorSettingsComponent},
      {path: 'students', component: TutorStudentsComponent},
      {path: 'parents', component: TutorParentsComponent}
    ]
  },
  {
    path: 'stdProfile', canActivate: [StdGuard], component: StdDefaultComponent,
    children: [
      {path: '', component: StdDashboardComponent},
      {path: 'profile', component: StdProfileComponent},
      {path: 'messages', component: StdMessagesComponent},
      {path: 'settings', component: StdSettingsComponent},
      {path: 'tutors', component: StdTutorsComponent}
    ]
  },
  {
    path: 'parentProfile', canActivate: [ParentGuard], component: PDefaultComponent,
    children: [
      {path: '', component: PdashboardComponent},
      {path: 'messages', component: PmessagesComponent},
      {path: 'profile', component: PprofileComponent},
      {path: 'settings', component: PsettingsComponent},
      {path: 'tutors', component: PtutorsComponent}
    ]
  },
  {path: '**', component: PnfoundComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
