import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { Router } from '@angular/router';

@Component({
  selector: 'app-std-header',
  templateUrl: './std-header.component.html',
  styleUrls: ['./std-header.component.css']
})
export class StdHeaderComponent implements OnInit {

  @Output() toggleSideBarOnClick: EventEmitter<any> = new EventEmitter();

  constructor(private router: Router) { }

  ngOnInit() {
  }
  toggleSideBar() {
    this.toggleSideBarOnClick.emit();
    setTimeout(() => {
      window.dispatchEvent(
        new Event('resize')
    );
    }, 300);
  }
  logout() {
    localStorage.clear();
    this.router.navigate(['/stdLogin']);
  }

}
