import { Component, OnInit, Input } from '@angular/core';
import * as Highcharts from 'highcharts';

@Component({
  selector: 'app-stds-chart',
  templateUrl: './stds-chart.component.html',
  styleUrls: ['./stds-chart.component.css']
})
export class StdsChartComponent implements OnInit {

  @Input() label: string;
  @Input() total: string;
  @Input() percentage: string;
  @Input() data = [];
  chartOptions: {};
  Highcharts = Highcharts;

  constructor() { }

  ngOnInit() {
    this.chartOptions = {

      title: {
        text: null,
        backgroundColor: null,
        borderWidth: 0,
        margin: [2, 2, 2, 2],
        height: 130
      },

      tooltip: {
        border: true
      },
      legend: {
        enabled: false
      },
      credits: {
        enabled: false
      },
      exporting: {
        enabled: false
      },
      xAxis: {
        labels: {
          enabled: false
        },
        title: {
          text: null
        },
        startOnTick: false,
        endOnTick: false,
        tickOptions: []
      },
      yAxis: {
        labels: {
          enabled: false
        },
        title: {
          text: null
        },
        startOnTick: false,
        endOnTick: false,
        tickOptions: []
      },
      series: [{
        data: this.data,
        pointStart: 1
      }]
    };
    setTimeout(() => {
      window.dispatchEvent(
        new Event('resize')
      );
    }, 300);

  }
}
