import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { StdService } from '../../../shared/services/std.service';

@Component({
  selector: 'app-stdlogin',
  templateUrl: './stdlogin.component.html',
  styleUrls: ['./stdlogin.component.css']
})
export class StdloginComponent implements OnInit {

error = false;
message = 'Invalid Email/Passowrd';
  constructor(private service: StdService, private router: Router) { }

  ngOnInit() {
  }
  login(fullform) {
    this.service.login(fullform.value).subscribe(data => {
      if (data) {
        this.service.StoreUserData(data);
      }
    }, err => {
      console.error(err);
      this.error = true;
    });
    }

}
